.data {
  color: #03002C;
  padding: 40px 0;
  &__title {
    border-bottom: 4px solid #DBE0F2;
    padding-bottom: 24px;
  }
  &__wrap {
    border: 1px solid #DBDCDE;
    border-radius: 20px;
    padding: 40px 50px;
    margin-top: 40px;
  }
  &__group {
    border: 1px solid #DBE0F2;
    border-radius: 20px;
  }
  &__group-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    background: #F5F7FB;
    border-bottom: 1px solid #DBE0F2;
    border-radius: 20px;
    padding: 16px 20px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
  }
  &__group-body {
    padding: 20px;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &-check {
      display: flex;
      align-items: center;
      gap: 14px;
      margin-bottom: auto;
      cursor: pointer;
      i {
        width: 28px;
        height: 28px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #36317B;
        border-radius: 8px;
        img {
          max-width: 80%;
          max-height: 80%;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 500;
      }
    }
    &-input {
      position: relative;
      width: 150px;
      margin-top: 15px;
      input {
        width: 100%;
        height: 48px;
        background: #fff;
        border: 1px solid #DBE0F2;
        border-radius: 100px;
        padding: 0 30px 0 20px;
        font-size: 16px;
        line-height: 115%;
        font-weight: 500;
        color: #03002C;
        &::placeholder {
          color: #616B94;
        }
        &:focus {
          border-color: #03002C;
        }
      }
      input::-webkit-outer-spin-button,
      input::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
      span {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
        font-size: 16px;
        line-height: 120%;
        font-weight: 500;
      }
    }
  }
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 30px;
    padding: 40px 0;
  }
  &__group2-left {
    width: calc(100% - 390px);
  }
  &__group2-right {
    width: 360px;
    background: #F5F7FB;
    border-radius: 20px;
    padding: 30px;
  }
  &__group2-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__map {
    margin-top: 10px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 24px;
  }
  &__item2 {
    position: relative;
    width: 100%;
    height: 48px;
    display: flex;
    align-items: center;
    background: #fff;
    border: 1px solid #DBE0F2;
    border-radius: 100px;
    padding: 5px 80px 5px 20px;
    span:nth-child(1) {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
    span:nth-child(2) {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #616B94;
    }
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check2.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 40px;
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 28px;
        height: 28px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #36317B;
        border-radius: 8px;
      }
    }
  }
  &__subgroup {
    border-bottom: 1px solid #DBE0F2;
    padding: 24px 0;
    &:last-child {
      border-bottom: unset;
      padding-bottom: 0;
    }
    .data__item-input {
      margin-top: 0;
    }
  }
  &__subgroup-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-bottom: 16px;
    .data__subtitle {
      font-size: 16px;
    }
  }
  &__subgroup-delete {
    width: 14px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__btn {
    height: 60px;
    padding: 5px 90px;
    margin: 40px auto 0;
    span {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .data {
    &__group2-left {
      width: calc(100% - 330px);
    }
    &__group-right {
      width: 300px;
    }
  }
}

@media screen and (max-width: 992px) {
  .data {
    &__wrap {
      padding: 40px 30px;
      margin-top: 24px;
    }
    &__group2 {
      flex-direction: column;
      gap: 24px;
    }
    &__group2-left {
      width: 100%;
    }
    &__group2-right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 768px) {
  .data {
    &__wrap {
      border: none;
      border-radius: 0;
      padding: 0;
    }
    &__subtitle {
      font-size: 16px;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
    }
    &__item {
      &-check {
        gap: 10px;
        i {
          width: 20px;
          height: 20px;
          border-radius: 5px;
          img {
            max-width: 12px;
          }
        }
      }
      &-input {
        width: 100%;
        input {
          height: 38px;
          padding-left: 16px;
          font-size: 14px;
        }
        span {
          right: 16px;
          font-size: 14px;
        }
      }
    }
    &__group2-right {
      padding: 20px;
    }
    &__items2 {
      margin-top: 16px;
    }
    &__item2 {
      height: 38px;
      padding: 5px 60px 5px 16px;
      span:nth-child(1) {
        font-size: 14px;
      }
      span:nth-child(2) {
        right: 16px;
        font-size: 14px;
      }
    }
    &__subgroup {
      .data__subtitle {
        font-size: 14px;
      }
    }
    &__checkbox {
      label {
        padding-left: 32px;
        font-size: 14px;
        &:after {
          width: 20px;
          height: 20px;
          background-size: 12px 8px;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 44px;
      padding: 5px 10px;
      margin-top: 24px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .data {
    padding: 24px 0;
  }
}