.calculations {
  position: relative;
  padding: 40px 0;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #fff;
    border: 1px solid #DBDCDE;
    border-radius: 20px;
  }
  &__left {
    width: 450px;
    display: flex;
    flex-direction: column;
    padding: 40px 40px 280px 40px;
    .calculations__filter {
      display: none;
    }
  }
  &__right {
    width: calc(100% - 450px);
    display: flex;
    flex-direction: column;
    background: #F5F7FB;
    border-radius: 0 20px 20px 0;
    padding: 40px 50px 280px 30px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-bottom: 28px;
  }
  .update {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 1px solid #36317B;
    border-radius: 50%;
    &:hover {
      background: #36317B;
      svg path {
        fill: #fff;
      }
    }
    svg {
      max-width: 80%;
      max-height: 80%;
      path {
        transition: all 0.3s;
      }
    }
  }
  &-cards {
    flex-grow: 1;
  }
  &__filter {
    height: 50px;
    gap: 12px;
    padding: 5px 16px;
    &:hover {
      i svg path {
        fill: #36317B;
      }
    }
    i {
      width: 18px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
  &__slider {
    position: relative;
    flex-grow: 1;
    padding-top: 20px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -20px;
      width: calc(100% + 90px);
      height: 100%;
      border: 1px dashed #BEC4DC;
      border-left: unset;
      border-radius: 0 20px 20px 0;
      pointer-events: none;
    }
  }
  &__slider-btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-top: 16px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    flex-shrink: 0;
    margin: 0;
  }
  .swiper-pagination {
    position: static;
    width: auto;
  }
  &__btn {
    position: absolute;
    left: 40px;
    bottom: 168px;
    height: 60px;
    max-width: 370px;
    width: 100%;
    padding: 5px 10px;
    i {
      width: 19px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      gap: 12px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 18px;
    }
  }
  .circle {
    position: absolute;
    top: 24px;
    right: 36px;
    width: 164px;
    height: 164px;
    background: #0BAE5F;
    filter: blur(230px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: 36px;
    bottom: 24px;
    width: 164px;
    height: 164px;
    background: #0BAE5F;
    filter: blur(150px);
    border-radius: 50%;
    pointer-events: none;
  }
}
.calculation-card {
  background: #fff;
  border: 1px solid #DBE0F2;
  border-radius: 20px 0 0 20px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    background: #F5F7FB;
    border-radius: 20px 0 0 0;
    padding: 16px 20px;
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 20px;
    line-height: 115%;
    font-weight: 600;
    color: #03002C;
  }
  .edit,
  .delete {
    width: 18px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #03002C;
      }
    }
    svg {
      max-width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__group {
    position: relative;
    padding: 24px 20px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% - 40px);
      height: 1px;
      background: #DBE0F2;
      pointer-events: none;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 16px;
  }
  &__item {
    &-title {
      font-size: 16px;
      line-height: 115%;
      font-weight: 600;
      color: #03002C;
      margin-bottom: 8px;
    }
    &-text {
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
    }
  }
  &__group2 {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 100px;
    padding: 24px 20px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% - 40px);
      height: 1px;
      background: #DBE0F2;
      pointer-events: none;
    }
  }
  &__items2 {
    grid-template-columns: 100%;
  }
  &__subgroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 16px;
    line-height: 115%;
    font-weight: 500;
    color: #36317B;
    i {
      width: 8px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__date {
    text-align: center;
    padding: 24px 20px;
    font-size: 14px;
    line-height: 115%;
    font-weight: 500;
    color: #555B71;
  }
}
.bank-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 20px;
  padding: 16px 16px 24px;
  &--disabled {
    position: relative;
    filter: blur(6px);
    .bank-card__img,
    .bank-card__desc {
      pointer-events: none;
    }
  }
  &__images {
    position: relative;
    width: 100%;
    height: 100px;
    padding-bottom: 45%;
  }
  &__images--1 {
    max-width: 160px;
    margin: 0 auto;
    .bank-card__img {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    .bank-card__name {
      font-size: 12px;
    }
  }
  &__images--2 {
    .bank-card__img:nth-child(1) {
      left: 0;
      bottom: 0;
      width: 60%;
      padding-bottom: 37%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .bank-card__img:nth-child(2) {
      top: 0;
      right: 0;
      width: 60%;
      padding-bottom: 37%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .bank-card__name {
      font-size: 8.5px;
    }
  }
  &__images--3 {
    .bank-card__img:nth-child(1) {
      left: 0;
      bottom: 0;
      width: 50%;
      padding-bottom: 32%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .bank-card__img:nth-child(2) {
      top: 50%;
      right: 0;
      transform: translate(-50%, -50%);
      width: 50%;
      padding-bottom: 32%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .bank-card__img:nth-child(3) {
      top: 0;
      right: 0;
      width: 50%;
      padding-bottom: 32%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .bank-card__name {
      font-size: 7.5px;
    }
  }
  &__img {
    position: absolute;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
  }
  &__name {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 10px 12px;
    line-height: 115%;
    font-weight: 600;
    color: #fff;
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }
  &__title {
    min-height: 52px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #03002C;
    border-bottom: 1px solid #DBE0F2;
    padding-bottom: 12px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    padding-top: 12px;
    margin-bottom: auto;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    font-weight: 500;
    color: #555B71;
    span {
      display: inline-block;
      color: #03002C;
    }
  }
  &__btn {
    height: 40px;
    width: 100%;
    padding: 5px 10px;
    margin-top: 20px;
    span {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .calculations {
    &__left {
      width: 400px;
    }
    &__right {
      width: calc(100% - 400px);
    }
    &__btn {
      max-width: 318px;
    }
  }
}

@media screen and (max-width: 992px) {
  .calculations {
    &__wrap {
      flex-direction: column;
      background: transparent;
      border: none;
      border-radius: 0;
    }
    &__left {
      width: 100%;
      padding: 0;
      .calculations__filter {
        width: 50px;
        display: flex;
        padding: 0;
        span {
          display: none;
        }
        i {
          svg {
            max-width: 80%;
            max-height: 80%;
          }
        }
      }
    }
    &__right {
      width: 100%;
      border: 1px solid #DBE0F2;
      border-radius: 20px;
      padding: 24px 20px;
      margin-top: -14px;
      .calculations__filter {
        display: none;
      }
    }
    &__group {
      display: flex;
      align-items: center;
      gap: 10px;
    }
    &__slider {
      padding-top: 0;
      &:after {
        display: none;
      }
    }
    &__slider-btns {
      justify-content: center;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    &__btn {
      position: static;
      max-width: 100%;
      margin-top: 24px;
    }
    .circle,
    .circle2 {
      display: none;
    }
  }
  .calculation-card {
    border-bottom: unset;
    border-radius: 20px 20px 0 0;
    &__top {
      border-radius: 20px 20px 0 0;
    }
    &__group2 {
      gap: 0;
    }
    &__items2 {
      border-bottom: 1px solid #DBE0F2;
      padding-bottom: 24px;
    }
    &__subgroup {
      padding-top: 24px;
    }
    &__date {
      padding-bottom: 34px;
    }
  }
}

@media screen and (max-width: 576px) {
  .calculations {
    padding: 24px 0;
    .update,
    &__filter {
      width: 36px !important;
      height: 36px !important;
    }
    &__btn {
      height: 48px;
      span {
        font-size: 14px;
      }
    }
  }
  .calculation-card {
    &__top {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
    &__title {
      width: 100%;
      justify-content: space-between;
    }
  }
}

@media screen and (max-width: 450px) {
  .calculations {
    &__right {
      overflow: hidden;
    }
    .swiper {
      overflow: unset;
    }
  }
}