.quiz {
  position: relative;
  padding: 40px 0;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    border-bottom: 4px solid #DBE0F2;
    padding-bottom: 24px;
    color: #03002C;
  }
  .progress-bar {
    position: relative;
    display: block;
    width: 100%;
    height: 4px;
    background: #DBE0F2;
    border-radius: 9px;
    margin-top: 24px;
    span {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      background: #0BAE5F;
      border-radius: 9px;
    }
  }
  &-body {
    background: #fff;
    border: 1px solid #DBDCDE;
    border-radius: 20px;
    padding: 40px 50px;
    margin-top: 40px;
  }
  &-step {
    display: none;
    &__title {
      font-size: 18px;
      line-height: 115%;
      font-weight: 600;
      color: #03002C;
    }
    &--active {
      display: block;
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    align-items: start;
    gap: 20px;
    margin-top: 30px;
  }
  &__checkboxes-left,
  &__checkboxes-right {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__checkboxes-left {
    .quiz__checkbox {
      label {
        background: #EDF1FF;
      }
    }
  }
  &__checkbox {
    position: relative;
    &:hover {
      label {
        border-color: #03002C;
      }
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #0BAE5F;
      border-color: #0BAE5F !important;
      color: #fff;
      i {
        opacity: 1;
      }
    }
    label {
      position: relative;
      height: 60px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;
      gap: 4px;
      border: 1px solid #E2E2E4;
      border-radius: 100px;
      padding: 10px 50px 10px 20px;
      font-size: 18px;
      line-height: 115%;
      font-weight: 500;
      color: #03002C;
      i {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
        width: 20px;
        flex-shrink: 0;
        opacity: 0;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        display: block;
        font-size: 14px;
      }
    }
    &-input {
      margin-top: 16px;
      input {
        width: 100%;
        height: 60px;
        border: 1px solid #E2E2E4;
        border-radius: 100px;
        padding: 0 20px;
        font-size: 18px;
        line-height: 115%;
        font-weight: 500;
        color: #03002C;
        &::placeholder {
          color: rgba(3, 0, 44, 0.5);
        }
      }
    }
  }
  &__checkbox2 {
    label {
      height: 70px;
    }
  }
  &__checkbox-wrap {
    .item-hidden {
      display: none;
    }
  }
  .active {
    .item-hidden {
      display: block;
    }
  }
  &__checkbox-add {
    margin-top: 16px;
  }
  .checkbox-add__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  .checkbox-add__item {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 16px;
    &--1 {
      order: -1;
    }
    &-inputs {
      flex-grow: 1;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    .nice-select {
      height: 60px;
      width: 100%;
      display: flex;
      align-items: center;
      border: 1px solid #E2E2E4;
      white-space: normal;
      border-radius: 100px;
      padding: 0 36px 0 20px;
      font-size: 18px;
      line-height: 115%;
      font-weight: 500;
      color: rgba(3, 0, 44, 0.5);
      &--active {
        color: #03002C;
        &:after {
          opacity: 1 !important;
        }
      }
      &:after {
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
        width: 14px;
        height: 8px;
        background-image: url("../img/bottom-arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        opacity: 0.5;
      }
      .current {
        white-space: nowrap;
        overflow: hidden;
      }
      .list {
        width: 100%;
        li {
          border: unset !important;
          padding: 8px 16px;
          font-size: 18px;
          line-height: 115%;
        }

      }
    }
    .open {
      border-color: #03002C;
    }
    &-input input {
      width: 100%;
      height: 60px;
      border: 1px solid #E2E2E4;
      border-radius: 100px;
      padding: 0 20px;
      font-size: 18px;
      line-height: 115%;
      font-weight: 500;
      color: #03002C;
      &::placeholder {
        color: rgba(3, 0, 44, 0.5);
      }
      &:focus {
        border-color: #03002C;
      }
    }
    &-btn {
      width: 60px;
      height: 60px;
      flex-shrink: 0;
      &:hover {
        svg path {
          stroke: #fff;
        }
      }
      svg {
        max-width: 80%;
        max-height: 80%;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
  &__nav {
    display: flex;
    justify-content: center;
    margin-top: 40px;
  }
  &__btn {
    height: 60px;
    background: #03045E;
    padding: 5px 90px;
    span {
      font-size: 18px;
    }
  }
  .circle {
    position: absolute;
    top: 150px;
    right: 36px;
    width: 164px;
    height: 164px;
    background: #0BAE5F;
    filter: blur(230px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: 0;
    bottom: 180px;
    width: 164px;
    height: 164px;
    background: #0BAE5F;
    filter: blur(230px);
    border-radius: 50%;
    pointer-events: none;
  }
}

@media screen and (max-width: 992px) {
  .quiz {
    &-body {
      padding: 40px 30px;
    }
    &__checkboxes {
      grid-template-columns: 100%;
      gap: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .quiz {
    &-body {
      border: none;
      border-radius: 0;
      padding: 0;
      margin-top: 24px;
    }
    &__checkboxes {
      gap: 12px;
      margin-top: 24px;
    }
    &__checkboxes-left,
    &__checkboxes-right {
      gap: 12px;
    }
    &__checkbox {
      label {
        height: 40px;
        padding: 0 16px;
        font-size: 14px;
        span {
          font-size: 12px;
        }
        i {
          right: 16px;
          width: 16px;
        }
      }
      &-input {
        margin-top: 12px;
        input {
          height: 40px;
          padding: 0 16px;
          font-size: 14px;
        }
      }
    }
    &__checkbox2 {
      label {
        height: 54px;
        padding: 0 16px;
        font-size: 14px;
      }
    }
    &__checkbox-add {
      margin-top: 12px;
    }
    .checkbox-add__items {
      gap: 12px;
    }
    .checkbox-add__item {
      gap: 12px;
      &-inputs {
        grid-template-columns: repeat(2, calc(50% - 8px / 2));
        gap: 8px;
      }
      .nice-select {
        height: 40px;
        padding: 0 24px 0 16px;
        font-size: 14px;
        .list {
          li {
            font-size: 12px;
          }

        }
      }
      .open {
        border-color: #03002C;
      }
      &-input input {
        height: 40px;
        padding: 0 16px;
        font-size: 14px;
      }
      &-btn {
        width: 40px;
        height: 40px;
      }
    }
    &__nav {
      margin-top: 24px;
    }
    &__btn {
      width: 100%;
      height: 44px;
      padding: 5px 10px;
      span {
        font-size: 14px;
      }
    }
    .circle,
    .circle2 {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .quiz {
    padding: 24px 0;
    .checkbox-add__item {
      position: relative;
      flex-direction: column;
      &-inputs {
        grid-template-columns: 100%;
      }
      &-input {
        width: calc(100% - 48px);
      }
      &-btn {
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
  }
}