.login {
  height: 100%;
  &__inner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-left: max(30px, calc(100% - 1320px) / 2);
  }
  &__desc {
    max-width: 690px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    gap: 30px;
    padding: 44px 30px 30px 0;
    overflow-y: auto;
    @media screen and (min-width: 769px) {
      .circle,
      .circle2 {
        display: none;
      }
    }
  }
  &__img {
    position: relative;
    height: 100%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F5F7FB;
    padding: 30px;
    overflow: hidden;
    img {
      position: relative;
      z-index: 2;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__logo {
    width: 190px;
    img {
      width: 100%;
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 20px;
    &-wrap {
      position: relative;
      z-index: 2;
      max-width: 560px;
      width: 100%;
    }
    &-icon {
      width: 110px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-title {
      text-align: center;
      margin-top: 28px;
    }
    &-group {
      input {
        height: 60px;
        font-size: 18px;
        color: #616B94;
        &::placeholder {
          color: rgba(97, 107, 148, 0.6);
        }
      }
    }
    &-btns {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
    }
    @media screen and (min-width: 993px) {
      &-btns2 {
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 10px / 2));
        gap: 10px;
      }
    }
    &-btn {
      height: 60px;
      padding: 5px 10px;
      span {
        font-size: 18px;
      }
    }
    &-checkboxes {
      display: grid;
      grid-template-columns: 100%;
      text-align: start;
      gap: 24px;
      margin-top: 8px;
    }
    &-check {
      label {
        font-size: 16px;
        color: #616B94;
      }
    }
    .tab__content {
      display: none;
      &--active {
        display: grid;
      }
    }
    .tab {
      cursor: pointer;
      transition: all 0.3s;
    }
    .tab--active {
      background: #36317B;
      color: #fff;
    }
  }
  .forgot-password,
  .back,
  .code {
    font-size: 16px;
    line-height: 115%;
    font-weight: 500;
    color: #616B94;
    margin-top: 8px;
  }
  &__text {
    position: relative;
    z-index: 2;
    font-size: 13px;
    line-height: 115%;
    color: rgba(0, 0, 0, 0.4);
  }
  .circle {
    position: absolute;
    top: -190px;
    right: -190px;
    width: 340px;
    height: 340px;
    background: #0BAE5F;
    filter: blur(240px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    left: -140px;
    bottom: -140px;
    width: 240px;
    height: 240px;
    background: #0BAE5F;
    filter: blur(170px);
    border-radius: 50%;
    pointer-events: none;
  }
}

@media screen and (max-width: 1400px) {
  .login {
    &__desc {
      width: 50%;
    }
    &__img {
      width: 50%;
    }
  }
}

@media screen and (max-width: 992px) {
  .login {
    &__inner {
      padding-left: max(15px, calc(100% - 1320px) / 2);
    }
    &__logo {
      width: 112px;
    }
    &__form {
      &-icon {
        width: 80px;
      }
      &-title {
        margin-top: 20px;
      }
      &-group {
        input {
          height: 45px;
          font-size: 14px;
        }
      }
      &-btn {
        height: 45px;
        span {
          font-size: 14px;
        }
      }
      &-check {
        label {
          font-size: 14px;
        }
      }
    }
    .forgot-password,
    .back,
    .code {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 768px) {
  .login {
    &__inner {
      padding: 30px 15px;
      overflow-x: hidden;
      overflow-y: auto;
    }
    &__desc {
      position: relative;
      max-width: 100%;
      width: 100%;
      padding: 0;
      overflow-y: unset;
      .circle {
        top: unset;
        right: -50px;
        bottom: -90px;
        width: 140px;
        height: 140px;
      }
      .circle2 {
        top: unset;
        left: -50px;
        bottom: -90px;
        width: 140px;
        height: 140px;
      }
    }
    &__img {
      display: none;
    }
  }
}