.account {
  padding: 40px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    border: 1px solid #DBDCDE;
    border-radius: 20px;
    padding: 40px;
  }
  &__left {
    width: 46%;
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-right: 30px;
  }
  &__right {
    width: 54%;
    background: #F5F7FB;
    border-radius: 20px;
    padding: 80px 0;
    overflow: hidden;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 115%;
    font-weight: 600;
    color: #03002C;
    margin-top: 40px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
    margin-bottom: auto;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 14px;
    i {
      width: 20px;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 115%;
    }
  }
  &__phone,
  &__email {
    &:hover {
      color: #0BAE5F;
    }
  }
  &__btns {
    max-width: 360px;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__btn {
    width: 100%;
    height: 60px;
    padding: 5px 20px;
    span {
      font-size: 18px;
    }
  }
  .btn-red {
    border-color: #FE3131;
    color: #FE3131;
    &:hover {
      background: #FE3131;
      color: #fff;
    }
  }
  &__img {
    position: relative;
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  .circle {
    position: absolute;
    top: 50%;
    left: -174px;
    transform: translate(0, -50%);
    width: 280px;
    height: 280px;
    background: #0858F4;
    filter: blur(230px);
    border-radius: 50%;
    opacity: 0.3;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 280px;
    height: 280px;
    background: #0858F4;
    filter: blur(230px);
    border-radius: 50%;
    opacity: 0.3;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    top: 50%;
    right: -174px;
    transform: translate(0, 50%);
    width: 280px;
    height: 280px;
    background: #0858F4;
    filter: blur(230px);
    border-radius: 50%;
    opacity: 0.3;
    pointer-events: none;
  }
  &__group {
    position: relative;
    z-index: 2;
    max-width: 480px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0 30px;
    margin: 80px auto 0;
  }
  &__subtitle2 {
    line-height: 120%;
    color: #03002C;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    color: #555B71;
    margin-top: 20px;
  }
  .promo-code {
    position: relative;
    width: 100%;
    margin-top: 40px;
    &__desc {
      height: 60px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;
      text-align: start;
      background: #fff;
      border: 1px solid #36317B;
      border-radius: 100px;
      padding: 5px 80px 5px 20px;
    }
    &__title {
      font-size: 14px;
      line-height: 115%;
      font-weight: 500;
      color: #555B71;
    }
    &__text {
      font-size: 18px;
      line-height: 115%;
      font-weight: 500;
      color: #36317B;
      margin-top: 2px;
    }
    &__btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 60px;
      height: 60px;
      background: #36317B;
      border-radius: 50%;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .account {
    &__inner {
      padding: 30px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 24px;
    }
    &__items {
      gap: 12px;
      margin-top: 20px;
    }
    &__item {
      span {
        font-size: 14px;
      }
    }
    &__btns {
      margin-top: 24px;
    }
    &__btn {
      height: 44px;
      span {
        font-size: 14px;
      }
    }
    &__text {
      font-size: 14px;
      margin-top: 16px;
    }
    .promo-code {
      margin-top: 24px;
      &__desc {
        height: 48px;
        padding: 5px 68px 5px 20px;
      }
      &__title {
        font-size: 12px;
      }
      &__text {
        font-size: 14px;
      }
      &__btn {
        width: 48px;
        height: 48px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .account {
    &__inner {
      flex-direction: column;
      gap: 30px;
      border: none;
      padding: 0;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      padding: 20px 16px;
    }
    &__group {
      max-width: 420px;
      padding: 0;
      margin-top: 30px;
    }
    &__btns {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .account {
    padding: 24px 0;
  }
}